<template>
  <div class = "timing">
     <div class="my_title">
        <h2>番茄钟</h2>
     </div>
     <div class = "clock">
        <div class = 'pie zindex' v-bind:style="{transform:'rotate('+ clock.pie2+'deg)',background:clock.bgColor}">
        </div>
        <div class = "pie bg" v-bind:style="{transform:'rotate('+ clock.pie1+'deg)'}"></div>
        <div class="timenum"> {{time.min}}:{{time.sec}}</div>
    </div>
     <div class="block_box">
     <button type= "button" @click="startTiming" class="block_css">{{title}}</button>
     </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      title:"开始计时",
      time: {
        sec: '00',
        min: '25',
        workTime: '25',
      },
      timing: {
        isTiming: false
      },
      doneTime: {
        startTime: '',
        endTime: ''
      },
      clock: {
        pie1: 0,
        pie2: 0,
        bgColor: '#fff',
        j: 0
      },
      show: {
        bool: false,
        content: ''
      }
    }
  },
  methods: {
    startTiming(){
      if (this.timing.isTiming === false) {
        this.title = "结束计时";
        var date = new Date();
        this.doneTime.startTime = date.getHours() + ':' + this.addZero(date.getMinutes());
        this.time.min = this.time.workTime;
        this.timing.isWorking = true;
        this.timing.isTiming = true;
        document.title = this.time.min + ':' + this.time.sec
        this.timeID = window.setTimeout(this.updateProgress, 1000)
      }else {
        this.title = "开始计时";
        this.timing.isWorking = false
        this.timing.isTiming = false
        this.time.min = this.time.workTime
        this.time.sec = '00'
        this.reset()
        window.clearTimeout(this.timeID)
      }
    },
    addZero (value) {
      if (value < 10) {
        value = '0' + value
      }
      return value + ''
    },
    updateProgress () {
      var step = this.timing.isWorking ? 6 / this.time.workTime : 6 / this.time.restTime
      this.clock.j++
      this.clock.pie1 = step * this.clock.j
      if (this.clock.pie1 === 180) {
        this.clock.pie2 = 180
        this.clock.bgColor = '#F08080'
      }
      if (this.time.min !== '00' && this.time.sec === '00') {
        this.time.min = this.addZero(+this.time.min - 1)
        this.time.sec = '59'
        this.timeID = window.setTimeout(this.updateProgress, 1000)
        document.title = this.time.min + ':' + this.time.sec
      } else if (this.time.sec !== '00') {
        this.time.sec = this.addZero(this.time.sec - 1)
        document.title = this.time.min + ':' + this.time.sec
        this.timeID = window.setTimeout(this.updateProgress, 1000)
      } 
    },
    reset () {
      this.clock.j = 0
      this.clock.pie1 = 0
      this.clock.pie2 = 0
      this.clock.bgColor = '#fff'
    }
  }
}
</script>
<style>
.my_title{
    text-align: center;
    width: 100%;
}
.block_box{
    width: 100%;
    text-align: center;
    margin-top: 50px;
}
.block_css{
    outline: none;
    width: 300px;
    height: 50px;
    background: rgb(252,100,100);
    border-radius:15px;
    font-size: 20px;
    font-weight: bolder;
    color: white;
}
.clock{
      position:relative;
      font-size:54px;
      margin:64px auto 40px;
      border:8px solid #ccc;
      height:200px;
      width:200px;
      border-radius: 109px;
      box-shadow: 0 0 0 10px #ff6666;
}
.pie{  
      top:0;
      left:0;
      position:absolute;  
      width:184px;  
      height:184px;  
      border-radius:92px;  
      clip: rect(0px, 92px, 184px, 0px);
      transform:rotate(0deg);
}  
.bg {
     background:#F08080;  
     }
.zindex{
  z-index:1;
} 
.clock .timenum{
    position:absolute;
    z-index:2;
    text-align: center;
    line-height:184px;
    left:25px;
}
</style>


